.iframe{
	width:100%;
	height:500px;
}
.dllist{
	dl{
		display:table-row;
		>*{
			display:table-cell;
			padding-bottom:10px;
		}
	}
	dt{
		white-space:nowrap;
		color:#424f88;
		&:before {
			content: '';
			width:4px;
			height:4px;
			display:inline-block;
			overflow:hidden;
			border-radius:50%;
			background-color:#b8b8b8;
			vertical-align:middle;
			margin-right:6px;
		}
		position:relative;
		padding-right:8px;
		&:after {
			content: ':';
			position:absolute;
			right:0;
			top:0;
		}
	}
	dd{
		padding-left:10px;
	}
	.bu2+&{margin-left:20px;}
}
